*{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
body{
  padding:60px;
}
input{
  border-color: orchid;
  border-radius: 5px;
  color: #009688;
  font-size: 1.3rem;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid #9c27b0;;
  border-radius: 5px;
  margin: 0px 0px 10px 10px;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}
